* {
    margin: 0px;
    box-sizing: border-box;
}



@mixin dis($dis: flex, $jus: center, $align: center, $flex: row, $wrap: nowrap, $color: snow) {
    display: $dis;
    justify-content: $jus;
    align-items: $align;
    flex-direction: $flex;
    flex-wrap: $wrap;
    color: $color;
}

.border1 {
    @include dis($flex: column, $jus: space-around);
    height: 100vh;
    border: 1px solid red;
    background-color: rgb(244, 239, 239);



    @media (max-width:768px) {
        height: 120%;
    }


    .box1 {
        width: 90%;
        padding: 20px;
        background-color: purple;
        color: aliceblue;
        font-size: 2em;
        text-transform: capitalize;
    }

    .box2 {
        width: 90%;
        height: 80%;
        padding: 20px;
        // border: 1px solid blue;
        text-transform: capitalize;

        @include dis($flex: row, $jus: space-around, $color: black);


        @media(max-width:768px) {
            @include dis($flex: column, $jus: space-around, $color: black);
        }

        .lft {
            width: 20%;
            height: 90%;
            // border: 1px solid blue;
            align-self: flex-start;
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: flex-start;

            @media (max-width:768px) {
                width: 95%;
            }

            .ul {
                align-self: flex-start;
                width: 90%;
                box-shadow: 2px 2px rgba(0, 0, 0, 0.558);
                margin: 10px;
                font-size: 1.4em;
                color: aliceblue;
                padding: 10px;
                background-color: rgba(97, 74, 245, 0.705);
                transition: 1s;

                @media (max-width:952px) {
                    font-size: 1.1em;

                }

                &:hover {
                    transform: scaleX(1.1);
                }

            }
        }

        .centr {
            align-self: flex-start;
            width: 50%;
            padding: 15px;
            font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
            font-size: 1.4em;
            // border: 1px solid blue;

            @media (max-width:952px) {
                font-size: 1.1em;

            }

            @media (max-width:768px) {
                width: 95%;
            }
        }

        .rgt {
            align-self: flex-start;
            width: 25%;
            height: 90%;
            // border: 1px solid blue;
            color: aliceblue;
            background-color: rgba(33, 164, 246, 0.628);

            dl {

                display: flex;
                align-items: center;
                flex-direction: column;
                text-align: justify;
                padding: 35px;

                dt {
                    padding: 15px;
                    font-size: 1.2em;
                    font-weight: 800;


                    @media (max-width:952px) {
                        font-size: 1.1em;

                    }
                }


            }

            @media (max-width:768px) {
                width: 95%;
            }
        }
    }



    .left {
        width: 35%;
        height: 90%;
        @include dis($flex: row, $jus: space-evenly);
        border: 1px solid rgb(16, 16, 233);
    }

    .right {
        width: 35%;
        height: 90%;
        @include dis($flex: row, $jus: space-evenly);
        // border: 1px solid blue;
        // background-image: linear-gradient(skyblue, rgb(36, 36, 39), );
        box-shadow: 3px 3px rgba(93, 90, 90, 0.492), 6px 4px rgba(119, 136, 223, 0.492);
        position: relative;
        transition: 1.4s;
        overflow: hidden;

        .sun {
            position: absolute;

            width: 100px;
            height: 100px;
            // border: 1px solid gold;
            border-radius: 50%;
            background-image: radial-gradient(rgb(238, 213, 73), gold, rgb(226, 238, 56), rgb(231, 221, 118));
            transition: 1.3s;
            animation: wave linear infinite;
            animation-duration: 3s;
        }

        .moon {
            position: absolute;
            border-radius: 50%;
            background-image: repeating-radial-gradient(rgb(225, 215, 215), rgb(225, 215, 215), rgb(234, 209, 209), rgb(244, 221, 221));
            width: 120px;
            height: 120px;
            transition: 1.4s;

        }

        @keyframes wave {
            100% {
                width: 130px;
                height: 130px;
                box-shadow: 4px 4px goldenrod, 6px 6px goldenrod, -4px 4px goldenrod,
                    -6px 6px goldenrod, -4px -4px goldenrod,
                    -6px -6px goldenrod, 4px -4px goldenrod,
                    6px -6px goldenrod;
            }
        }
    }





}