* {
  margin: 0px;
  box-sizing: border-box;
}

.border1 {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  color: snow;
  height: 100vh;
  border: 1px solid red;
  background-color: rgb(244, 239, 239);
}
@media (max-width: 768px) {
  .border1 {
    height: 120%;
  }
}
.border1 .box1 {
  width: 90%;
  padding: 20px;
  background-color: purple;
  color: aliceblue;
  font-size: 2em;
  text-transform: capitalize;
}
.border1 .box2 {
  width: 90%;
  height: 80%;
  padding: 20px;
  text-transform: capitalize;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  color: black;
}
@media (max-width: 768px) {
  .border1 .box2 {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;
    color: black;
  }
}
.border1 .box2 .lft {
  width: 20%;
  height: 90%;
  align-self: flex-start;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
}
@media (max-width: 768px) {
  .border1 .box2 .lft {
    width: 95%;
  }
}
.border1 .box2 .lft .ul {
  align-self: flex-start;
  width: 90%;
  box-shadow: 2px 2px rgba(0, 0, 0, 0.558);
  margin: 10px;
  font-size: 1.4em;
  color: aliceblue;
  padding: 10px;
  background-color: rgba(97, 74, 245, 0.705);
  transition: 1s;
}
@media (max-width: 952px) {
  .border1 .box2 .lft .ul {
    font-size: 1.1em;
  }
}
.border1 .box2 .lft .ul:hover {
  transform: scaleX(1.1);
}
.border1 .box2 .centr {
  align-self: flex-start;
  width: 50%;
  padding: 15px;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-size: 1.4em;
}
@media (max-width: 952px) {
  .border1 .box2 .centr {
    font-size: 1.1em;
  }
}
@media (max-width: 768px) {
  .border1 .box2 .centr {
    width: 95%;
  }
}
.border1 .box2 .rgt {
  align-self: flex-start;
  width: 25%;
  height: 90%;
  color: aliceblue;
  background-color: rgba(33, 164, 246, 0.628);
}
.border1 .box2 .rgt dl {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: justify;
  padding: 35px;
}
.border1 .box2 .rgt dl dt {
  padding: 15px;
  font-size: 1.2em;
  font-weight: 800;
}
@media (max-width: 952px) {
  .border1 .box2 .rgt dl dt {
    font-size: 1.1em;
  }
}
@media (max-width: 768px) {
  .border1 .box2 .rgt {
    width: 95%;
  }
}
.border1 .left {
  width: 35%;
  height: 90%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  color: snow;
  border: 1px solid rgb(16, 16, 233);
}
.border1 .right {
  width: 35%;
  height: 90%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  color: snow;
  box-shadow: 3px 3px rgba(93, 90, 90, 0.492), 6px 4px rgba(119, 136, 223, 0.492);
  position: relative;
  transition: 1.4s;
  overflow: hidden;
}
.border1 .right .sun {
  position: absolute;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-image: radial-gradient(rgb(238, 213, 73), gold, rgb(226, 238, 56), rgb(231, 221, 118));
  transition: 1.3s;
  animation: wave linear infinite;
  animation-duration: 3s;
}
.border1 .right .moon {
  position: absolute;
  border-radius: 50%;
  background-image: repeating-radial-gradient(rgb(225, 215, 215), rgb(225, 215, 215), rgb(234, 209, 209), rgb(244, 221, 221));
  width: 120px;
  height: 120px;
  transition: 1.4s;
}
@keyframes wave {
  100% {
    width: 130px;
    height: 130px;
    box-shadow: 4px 4px goldenrod, 6px 6px goldenrod, -4px 4px goldenrod, -6px 6px goldenrod, -4px -4px goldenrod, -6px -6px goldenrod, 4px -4px goldenrod, 6px -6px goldenrod;
  }
}/*# sourceMappingURL=design.css.map */